#backDrop {
  width: 100%;
  height: 110vh;
  background-color: rgba(0, 0, 0, 0.722);
  position: absolute;
}



@media (max-width: 720px) and (min-width: 500px) {
  #backDrop {
    height: 115vh;
  }
}

@media (max-width: 500px) and (min-width: 0px) {
  #backDrop {
    height: 115vh;
  }
}
