.icon {
  font-family: "Material Icons", serif;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "liga";
}

:root {
  --main-color: #1a8fff;
  --text-color: rgb(83, 83, 83);
  --text-color-light: rgb(141, 141, 141);
  --border-color: #eee;
  --bg-color: #f9f9f9;
  --neutral-color: #fff;
}

#body {
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 300;
  box-sizing: border-box;
  color: black;
  position: relative;
}

header {
  display: block;
  width: 100%;
  padding: 0.75em 0;
  border-bottom: 1px solid #eee;
  border-bottom: 1px solid var(--border-color);
  background: #fff;
  background: var(--neutral-color);
}

.dayInHeader {
  width: 20px;
  padding: 5px 10px;
}

header #logo {
  font-size: 175%;
  text-align: center;
  color: #1a8fff;
  color: var(--main-color);
  line-height: 1;
}

header #logo .icon {
  padding-right: 0.25em;
}

/* GRID */

.row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  box-sizing: border-box;
}

.row-middle {
  align-items: center;
  box-sizing: border-box;
}

.col {
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
  box-sizing: border-box;
}

.col-start {
  justify-content: flex-start;
  text-align: left;
}

.col-center {
  justify-content: center;
  text-align: center;
}

.col-end {
  justify-content: flex-end;
  text-align: right;
}

/* Calendar */

.calendar {
  display: block;
  position: relative;
  width: 280px;
  background: #fff;
  background: var(--neutral-color);
  border: 1px solid #eee;
  border: 1px solid var(--border-color);
  box-sizing: border-box;
}

.calendar .header {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 115%;
  padding: 0.2em 0;
  border-bottom: 1px solid #eee;
  border-bottom: 1px solid var(--border-color);
}

.calendar .header .icon {
  cursor: pointer;
  -webkit-transition: 0.15s ease-out;
  transition: 0.15s ease-out;
}

.calendar .header .icon:hover {
  -webkit-transform: scale(1.75);
          transform: scale(1.75);
  -webkit-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
  color: #1a8fff;
  color: var(--main-color);
}

.calendar .header .icon:first-of-type {
  margin-left: 1em;
}

.calendar .header .icon:last-of-type {
  margin-right: 1em;
}

.calendar .body .cell {
  position: relative;
  height: 2em;
  border-right: 1px solid #eee;
  border-right: 1px solid var(--border-color);
  overflow: hidden;
  cursor: pointer;
  background: #fff;
  background: var(--neutral-color);
  -webkit-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
}

.calendar .body .cell:hover {
  background: var(black);
  -webkit-transition: 0.5s ease-out;
  transition: 0.5s ease-out;
}

.calendar .body .selected {
  border-left: 10px solid transparent;
  -webkit-border-image: linear-gradient(45deg, #1a8fff 0%, #53cbf1 40%);
          border-image: linear-gradient(45deg, #1a8fff 0%, #53cbf1 40%);
  -webkit-border-image: linear-gradient(45deg, var(--main-color) 0%, #53cbf1 40%);
          border-image: linear-gradient(45deg, var(--main-color) 0%, #53cbf1 40%);
  border-image-slice: 1;
}

.calendar .body .row {
  border-bottom: 1px solid #eee;
  border-bottom: 1px solid var(--border-color);
}

.calendar .body .row:last-child {
  border-bottom: none;
}

.calendar .body .cell:last-child {
  border-right: none;
}

.calendar .body .cell .number {
  position: absolute;
  font-size: 82.5%;
  line-height: 1;
  top: 0.75em;
  right: 0.75em;
  font-weight: 700;
}

.calendar .body .disabled {
  color: rgb(179, 179, 179);
  pointer-events: none;
}

